/**
 * Project Detail Page - KHO MVG
 * Chi tit d n vi Google Maps v qun l zones
 */

import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Badge, Tab, Nav, Table, Modal, Alert, Form } from 'react-bootstrap';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import { useNotification } from '../../contexts/NotificationContext';
import axios from 'axios';
import LoadingSpinner from '../../components/Common/LoadingSpinner';
import ProjectMapView from '../../components/Map/ProjectMapView';
import TaskManager from '../../components/Tasks/TaskManager';
import './ProjectDetail.css';

function ProjectDetail() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { hasPermission } = useAuth();
  const { showNotification } = useNotification();
  
  const [loading, setLoading] = useState(true);
  const [project, setProject] = useState(null);
  const [zones, setZones] = useState([]);
  const [activeTab, setActiveTab] = useState('overview');
  const [showZoneModal, setShowZoneModal] = useState(false);
  const [selectedZone, setSelectedZone] = useState(null);
  const [showAddZoneModal, setShowAddZoneModal] = useState(false);
  const [showDocumentModal, setShowDocumentModal] = useState(false);
  const [uploadedDocuments, setUploadedDocuments] = useState([]);
  const [isDocumentServiceAvailable, setIsDocumentServiceAvailable] = useState(true);
  const [isHeaderCollapsed, setIsHeaderCollapsed] = useState(false);

  useEffect(() => {
    let scrollTimeout;
    const scrollThreshold = 500; // Tng ngng cun  n nh hn (trc l 100)
    const debounceDelay = 50; // Gim  tr debounce  phn ng nhanh hn mt cht

    const handleScroll = () => {
      clearTimeout(scrollTimeout);
      scrollTimeout = setTimeout(() => {
        if (window.scrollY > scrollThreshold) {
          setIsHeaderCollapsed(true);
        } else {
          setIsHeaderCollapsed(false);
        }
      }, debounceDelay);
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
      clearTimeout(scrollTimeout);
    };
  }, []);

  // Mock project data
  const mockProject = {
    id: parseInt(id),
    name: 'Kho xng Bnh Dng',
    code: 'KX-BD-001',
    description: 'Khu kho xng hin i vi y  tin ch v h tng logistics.',
    address: '123 ng ABC, Thun An, Bnh Dng',
    province: 'Bnh Dng',
    district: 'Thun An',
    ward: 'Phng An Thnh',
    latitude: 10.9045,
    longitude: 106.7213,
    status: 'operational',
    total_area: 15000,
    used_area: 12000,
    available_area: 3000,
    fixed_area: 500,
    owner_info: {
      name: 'Cng ty TNHH ABC',
      phone: '0123456789',
      email: 'contact@abc.com'
    },
    project_director: {
      name: 'Nguyn Vn Kin',
      phone: '0987654321',
      email: 'kien@abc.com',
      position: 'Gim c d n'
    },
    project_manager: {
      name: 'Trn Th Lin',
      phone: '0976543210',
      email: 'lien@abc.com',
      position: 'Ngi qun l d n'
    },
    legal_documents: {
      business_license: 'GP-123456789',
      construction_permit: 'XD-987654321',
      fire_safety_cert: 'PCCC-111222333'
    },
    created_at: '2024-01-15',
    created_by_username: 'admin'
  };

  const mockZones = [
    {
      id: 1,
      zone_code: 'A1',
      zone_name: 'Khu vc A1',
      area: 500,
      status: 'rented',
      rental_price: 150000,
      tenant_info: {
        customer_code: 'KH001',
        company_name: 'Cng ty XYZ',
        contact_person: 'Nguyn Vn A',
        contract_number: 'HD240001',
        end_date: '2024-12-31'
      }
    },
    {
      id: 2,
      zone_code: 'A2',
      zone_name: 'Khu vc A2',
      area: 750,
      status: 'available',
      rental_price: 140000,
      tenant_info: null
    },
    {
      id: 3,
      zone_code: 'B1',
      zone_name: 'Khu vc B1',
      area: 600,
      status: 'deposited',
      rental_price: 160000,
      tenant_info: {
        customer_code: 'KH002',
        company_name: 'Cng ty DEF',
        contact_person: 'Trn Th B',
        contract_number: 'HD240002',
        end_date: '2024-06-30'
      }
    },
    {
      id: 4,
      zone_code: 'C1',
      zone_name: 'Khu vc C1 - Vn phng',
      area: 200,
      status: 'maintenance',
      rental_price: 0,
      tenant_info: null
    }
  ];

  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setProject(mockProject);
      setZones(mockZones);
      setLoading(false);
    }, 1000);
  }, [id, mockProject, mockZones]);

  useEffect(() => {
    // Fetch uploaded documents for this project
    const fetchDocuments = async () => {
      try {
        const resp = await axios.get('/api/documents', { params: { resource_type: 'project', resource_id: id } });
        if (resp.data && resp.data.success) {
          const files = resp.data.data.files.map(f => ({
            id: f._id || f.id,
            name: f.originalname || f.name || f.filename,
            filename: f.filename,
            size: f.size,
            mimetype: f.mimetype,
            uploadedAt: f.uploaded_at || f.uploadedAt,
            category_id: f.category_id,
            resource_type: f.resource_type,
            resource_id: f.resource_id
          }));
          setUploadedDocuments(files);
        }
      } catch (err) {
        console.error('Error fetching documents', err);
        setIsDocumentServiceAvailable(false);
        showNotification(
          'Khng ti c danh sch ti liu. Vui lng th li sau.',
          'warning'
        );
      }
    };

    fetchDocuments();
  }, [id, showNotification]);

  const getStatusBadge = (status) => {
    const statusMap = {
      operational: { variant: 'success', label: 'Hot ng' },
      construction: { variant: 'warning', label: 'Xy dng' },
      planning: { variant: 'info', label: 'Ln k hoch' },
      maintenance: { variant: 'secondary', label: 'Bo tr' }
    };
    
    const statusInfo = statusMap[status] || { variant: 'secondary', label: status };
    return <Badge bg={statusInfo.variant}>{statusInfo.label}</Badge>;
  };

  const getZoneStatusBadge = (status) => {
    const statusMap = {
      available: { variant: 'danger', label: 'Cha cho thu', color: '#dc3545' },
      rented: { variant: 'success', label: ' cho thu', color: '#28a745' },
      deposited: { variant: 'warning', label: ' cc', color: '#ffc107' },
      maintenance: { variant: 'secondary', label: 'Bo tr', color: '#6c757d' }
    };
    
    const statusInfo = statusMap[status] || { variant: 'secondary', label: status, color: '#6c757d' };
    return (
      <Badge bg={statusInfo.variant} style={{ color: status === 'deposited' ? '#000' : '#fff' }}>
        {statusInfo.label}
      </Badge>
    );
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND'
    }).format(amount).replace('', '');
  };

  const formatArea = (area) => {
    return new Intl.NumberFormat('vi-VN').format(area) + ' m';
  };

  const handleZoneClick = (zone) => {
    setSelectedZone(zone);
    setShowZoneModal(true);
  };

  const calculateOccupancyStats = () => {
    const stats = {
      total: zones.length,
      available: zones.filter(z => z.status === 'available').length,
      rented: zones.filter(z => z.status === 'rented').length,
      deposited: zones.filter(z => z.status === 'deposited').length,
      maintenance: zones.filter(z => z.status === 'maintenance').length
    };
    
    const totalArea = zones.reduce((sum, zone) => sum + zone.area, 0);
    const rentedArea = zones.filter(z => z.status === 'rented').reduce((sum, zone) => sum + zone.area, 0);
    const occupancyRate = totalArea > 0 ? Math.round((rentedArea / totalArea) * 100) : 0;
    
    return { ...stats, occupancyRate, totalArea, rentedArea };
  };

  const handleAddZone = (newZone) => {
    setZones([...zones, newZone]);
    setShowAddZoneModal(false);
  };

  const handleEditZone = (updatedZone) => {
    setZones(zones.map(z => z.id === updatedZone.id ? updatedZone : z));
    setShowZoneModal(false);
  };

  const handleDeleteZone = (zoneId) => {
    if (window.confirm('Bn c chc mun xa kho ny?')) {
      setZones(zones.filter(z => z.id !== zoneId));
    }
  };

  const handleDocumentUpload = async (e) => {
    if (!isDocumentServiceAvailable) {
      showNotification('Dch v ti liu khng kh dng. Vui lng th li sau.', 'warning');
      return;
    }
    const files = Array.from(e.target.files);
    if (files.length === 0) return;
    const formData = new FormData();
    files.forEach(f => formData.append('files', f));
    formData.append('resource_type', 'project');
    formData.append('resource_id', id);

    try {
      const resp = await axios.post('/api/documents/upload', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });
      if (resp.data && resp.data.success) {
        // The server returns list of uploaded files metadata
        const uploaded = resp.data.data.files.map(f => ({
          id: f.id || f._id,
          name: f.originalname || f.filename,
          filename: f.filename,
          size: f.size,
          mimetype: f.mimetype,
          uploadedAt: new Date().toISOString(),
          resource_type: 'project',
          resource_id: id
        }));

        setUploadedDocuments(prevDocs => [...uploaded, ...prevDocs]);
        showNotification(` ti ln ${uploaded.length} ti liu.`, 'success');
      }
    } catch (error) {
      console.error('Upload failed', error);
      showNotification('Ti ln tht bi. Vui lng th li.', 'danger');
    } finally {
      e.target.value = '';
    }
  };

  const handleViewDocument = (doc) => {
    try {
      if (doc.filename) {
        const url = `/uploads/documents/${doc.filename}`;
        window.open(url, '_blank');
        showNotification(`ang m ti liu: ${doc.name}`, 'info');
        return;
      }

      if (doc.url) {
        window.open(doc.url, '_blank');
        showNotification(`ang m ti liu: ${doc.name}`, 'info');
        return;
      }

      showNotification('Khng c ng dn  m ti liu.', 'warning');
    } catch (err) {
      showNotification('Khng th m ti liu.', 'danger');
    }
  };

  const handleDownloadDocument = async (doc) => {
    try {
      if (doc.id) {
        const resp = await axios.get(`/api/documents/download/${doc.id}`, { responseType: 'blob' });
        const url = window.URL.createObjectURL(new Blob([resp.data]));
        const a = document.createElement('a');
        a.href = url;
        a.download = doc.name;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        window.URL.revokeObjectURL(url);
        showNotification(`ang ti xung ti liu: ${doc.name}`, 'info');
        return;
      }

      if (doc.url) {
        const a = document.createElement('a');
        a.href = doc.url;
        a.download = doc.name;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        showNotification(`ang ti xung ti liu: ${doc.name}`, 'info');
        return;
      }

      showNotification('Tp tin khng tn ti  ti xung.', 'warning');
    } catch (err) {
      console.error('Error downloading', err);
      showNotification('Ti xung tht bi.', 'danger');
    }
  };

  const handleDeleteDocument = async (docId) => {
    if (!window.confirm('Bn c chc mun xa ti liu ny?')) return;
    try {
      const resp = await axios.delete(`/api/documents/${docId}`);
      if (resp.data && resp.data.success) {
        setUploadedDocuments(prev => prev.filter(d => d.id !== docId));
        showNotification(' xa ti liu.', 'success');
      }
    } catch (err) {
      console.error('Delete failed', err);
      showNotification('Xa ti liu tht bi.', 'danger');
    }
  };

  const stats = calculateOccupancyStats();

  if (loading) {
    return <LoadingSpinner text="ang ti thng tin d n..." />;
  }

  if (!project) {
    return (
      <Container className="mt-5">
        <Alert variant="danger" className="text-center">
          <h4>Khng tm thy d n</h4>
          <p>D n vi ID {id} khng tn ti hoc  b xa.</p>
          <Button variant="primary" onClick={() => navigate('/projects')}>
            Quay li danh sch d n
          </Button>
        </Alert>
      </Container>
    );
  }

  return (
    <div className="project-detail-page p-4">
      <div className={`sticky-top-container ${isHeaderCollapsed ? 'collapsed' : ''}`}>
        {/* Header */}
        <div className={`project-header mb-4 ${isHeaderCollapsed ? 'collapsed' : ''}`}>
          <Row>
            <Col>
              <div className="d-flex justify-content-between align-items-start">
                <div>
                  <div className="d-flex align-items-center mb-2">
                    <Button 
                      variant="outline-secondary" 
                      size="sm" 
                      className="me-3"
                      onClick={() => navigate('/projects')}
                    >
                      <i className="fas fa-arrow-left"></i>
                    </Button>
                    <div>
                      <h2 className="mb-0">{project.name}</h2>
                      <div className="d-flex align-items-center mt-1">
                        <span className="text-muted me-3">M: {project.code}</span>
                        {getStatusBadge(project.status)}
                      </div>
                    </div>
                  </div>
                  <p className="text-muted mb-0">{project.address}</p>
                </div>
                
                <div className="d-flex gap-2">
                  {hasPermission('project_update') && (
                    <Button 
                      variant="outline-primary"
                      onClick={() => navigate(`/projects/${id}/edit`)}
                    >
                      <i className="fas fa-edit me-2"></i>
                      Chnh sa
                    </Button>
                  )}
                  <Button variant="outline-secondary">
                    <i className="fas fa-download me-2"></i>
                    Xut bo co
                  </Button>
                </div>
              </div>
            </Col>
          </Row>
        </div>

        {/* Statistics Cards - Responsive Layout */}
        <div className={`statistics-container mb-4 ${isHeaderCollapsed ? 'collapsed' : ''}`}>
        <Row className="g-3">
          <Col lg={3} md={6} sm={12}>
            <Card className="border-0 shadow-sm h-100">
              <Card.Body>
                <h6 className="text-muted mb-3">Tng Din Tch</h6>
                <div className="mb-3">
                  <div className="d-flex justify-content-between align-items-end mb-2">
                    <span className="fw-bold text-primary">{formatArea(project.total_area)}</span>
                    <i className="fas fa-expand-arrows-alt fa-lg text-primary opacity-25"></i>
                  </div>
                </div>
                <div className="small">
                  <div className="d-flex justify-content-between mb-2">
                    <span className="text-success"> cho thu:</span>
                    <span className="fw-bold text-success">{formatArea(zones.filter(z => z.status === 'rented').reduce((sum, z) => sum + z.area, 0))}</span>
                  </div>
                  <div className="d-flex justify-content-between mb-2">
                    <span className="text-warning"> cc:</span>
                    <span className="fw-bold text-warning">{formatArea(zones.filter(z => z.status === 'deposited').reduce((sum, z) => sum + z.area, 0))}</span>
                  </div>
                  <div className="d-flex justify-content-between">
                    <span className="text-danger">Cn trng:</span>
                    <span className="fw-bold text-danger">{formatArea(zones.filter(z => z.status === 'available').reduce((sum, z) => sum + z.area, 0))}</span>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={3} md={6} sm={12}>
            <Card className="border-0 shadow-sm h-100">
              <Card.Body>
                <h6 className="text-muted mb-3">T L Thu</h6>
                <div className="mb-3">
                  <div className="d-flex justify-content-between align-items-end mb-2">
                    <span className="fw-bold text-success">{stats.occupancyRate}%</span>
                    <i className="fas fa-percentage fa-lg text-success opacity-25"></i>
                  </div>
                  <div className="progress" style={{ height: '8px' }}>
                    <div className="progress-bar bg-success" style={{ width: `${stats.occupancyRate}%` }}></div>
                  </div>
                </div>
                <div className="small">
                  <div className="d-flex justify-content-between mb-1">
                    <span> thu:</span>
                    <span className="fw-bold">{stats.rented} kho</span>
                  </div>
                  <div className="d-flex justify-content-between mb-1">
                    <span> cc:</span>
                    <span className="fw-bold">{stats.deposited} kho</span>
                  </div>
                  <div className="d-flex justify-content-between">
                    <span>Cn trng:</span>
                    <span className="fw-bold">{stats.available} kho</span>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={3} md={6} sm={12}>
            <Card className="border-0 shadow-sm h-100">
              <Card.Body>
                <h6 className="text-muted mb-3">S Kho</h6>
                <div className="mb-3">
                  <div className="d-flex justify-content-between align-items-end mb-2">
                    <span className="fw-bold text-info">{stats.total}</span>
                    <i className="fas fa-th-large fa-lg text-info opacity-25"></i>
                  </div>
                </div>
                <div className="small">
                  <div className="d-flex justify-content-between mb-2">
                    <span className="text-success"> thu:</span>
                    <span className="fw-bold text-success">{stats.rented}</span>
                  </div>
                  <div className="d-flex justify-content-between mb-2">
                    <span className="text-warning"> cc:</span>
                    <span className="fw-bold text-warning">{stats.deposited}</span>
                  </div>
                  <div className="d-flex justify-content-between">
                    <span className="text-danger">Cn trng:</span>
                    <span className="fw-bold text-danger">{stats.available}</span>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={3} md={6} sm={12}>
            <Card className="border-0 shadow-sm h-100">
              <Card.Body>
                <h6 className="text-muted mb-3">Doanh Thu/Thng</h6>
                <div className="mb-3">
                  <div className="d-flex justify-content-between align-items-end mb-2">
                    <span className="fw-bold text-warning">
                      {formatCurrency(zones.filter(z => z.status === 'rented').reduce((sum, z) => sum + (z.rental_price * z.area), 0))}
                    </span>
                    <i className="fas fa-chart-line fa-lg text-warning opacity-25"></i>
                  </div>
                </div>
                <div className="small">
                  <div className="d-flex justify-content-between mb-1">
                    <span>T cho thu:</span>
                    <span className="fw-bold">
                      {formatCurrency(zones.filter(z => z.status === 'rented').reduce((sum, z) => sum + (z.rental_price * z.area), 0))}
                    </span>
                  </div>
                  <div className="d-flex justify-content-between">
                    <span>Tim nng:</span>
                    <span className="fw-bold">
                      {formatCurrency(zones.filter(z => z.status === 'available').reduce((sum, z) => sum + (z.rental_price * z.area), 0))}
                    </span>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
        </div>
      </div>

      {/* Main Content Tabs */}
      <Card className="border-0 shadow-sm">
        <Card.Header className="border-bottom-0">
          <Tab.Container activeKey={activeTab} onSelect={setActiveTab}>
            <Nav variant="tabs" className="border-0">
              <Nav.Item>
                <Nav.Link eventKey="overview">
                  <i className="fas fa-info-circle me-2"></i>
                  Thng tin chung
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="zones">
                  <i className="fas fa-th-large me-2"></i>
                  Qun l kho ({zones.length})
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="map">
                  <i className="fas fa-map me-2"></i>
                  Bn 
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="tasks">
                  <i className="fas fa-tasks me-2"></i>
                  Cng vic
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="documents">
                  <i className="fas fa-folder me-2"></i>
                  H s php l
                </Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="team">
                  <i className="fas fa-users me-2"></i>
                  i d n
                </Nav.Link>
              </Nav.Item>
            </Nav>
          </Tab.Container>
        </Card.Header>

        <Card.Body>
          <Tab.Container activeKey={activeTab}>
            <Tab.Content>
              {/* Overview Tab */}
              <Tab.Pane eventKey="overview">
                <Row>
                  <Col md={6}>
                    <h5 className="mb-3">Thng tin d n</h5>
                    <div className="info-group">
                      <div className="info-item">
                        <strong>Tn d n:</strong> {project.name}
                      </div>
                      <div className="info-item">
                        <strong>M d n:</strong> {project.code}
                      </div>
                      <div className="info-item">
                        <strong>M t:</strong> {project.description}
                      </div>
                      <div className="info-item">
                        <strong>a ch:</strong> {project.address}
                      </div>
                      <div className="info-item">
                        <strong>Tnh/Thnh:</strong> {project.province}
                      </div>
                      <div className="info-item">
                        <strong>Trng thi:</strong> {getStatusBadge(project.status)}
                      </div>
                    </div>
                  </Col>
                  <Col md={6}>
                    <h5 className="mb-3">Thng tin ch s hu</h5>
                    <div className="info-group">
                      <div className="info-item">
                        <strong>Tn:</strong> {project.owner_info?.name}
                      </div>
                      <div className="info-item">
                        <strong>in thoi:</strong> {project.owner_info?.phone}
                      </div>
                      <div className="info-item">
                        <strong>Email:</strong> {project.owner_info?.email}
                      </div>
                    </div>

                    <h5 className="mb-3 mt-4">Gim c d n</h5>
                    <div className="info-group">
                      <div className="info-item">
                        <strong>Tn:</strong> {project.project_director?.name}
                      </div>
                      <div className="info-item">
                        <strong>Chc v:</strong> {project.project_director?.position}
                      </div>
                      <div className="info-item">
                        <strong>in thoi:</strong> {project.project_director?.phone}
                      </div>
                      <div className="info-item">
                        <strong>Email:</strong> {project.project_director?.email}
                      </div>
                    </div>

                    <h5 className="mb-3 mt-4">Ngi qun l d n</h5>
                    <div className="info-group">
                      <div className="info-item">
                        <strong>Tn:</strong> {project.project_manager?.name}
                      </div>
                      <div className="info-item">
                        <strong>Chc v:</strong> {project.project_manager?.position}
                      </div>
                      <div className="info-item">
                        <strong>in thoi:</strong> {project.project_manager?.phone}
                      </div>
                      <div className="info-item">
                        <strong>Email:</strong> {project.project_manager?.email}
                      </div>
                    </div>

                    <h5 className="mb-3 mt-4">Thng k din tch</h5>
                    <div className="info-group">
                      <div className="info-item">
                        <strong>Tng din tch:</strong> {formatArea(project.total_area)}
                      </div>
                      <div className="info-item">
                        <strong> s dng:</strong> {formatArea(project.used_area)}
                      </div>
                      <div className="info-item">
                        <strong>Cn trng:</strong> {formatArea(project.available_area)}
                      </div>
                      <div className="info-item">
                        <strong>C nh:</strong> {formatArea(project.fixed_area)}
                      </div>
                    </div>
                  </Col>
                </Row>
              </Tab.Pane>

              {/* Zones Tab */}
              <Tab.Pane eventKey="zones">
                <div className="d-flex justify-content-between align-items-center mb-3">
                  <h5 className="mb-0">Danh sch kho</h5>
                  <div className="d-flex gap-2">
                    <Button 
                      variant="outline-success" 
                      size="sm"
                      onClick={() => {
                        const input = document.createElement('input');
                        input.type = 'file';
                        input.accept = '.dxf,.pdf,.dwg,.png,.jpg,.jpeg';
                        input.onchange = (e) => {
                          const file = e.target.files[0];
                          if (file) {
                            alert(` import bn v: ${file.name}`);
                          }
                        };
                        input.click();
                      }}
                    >
                      <i className="fas fa-file-import me-2"></i>
                      Import bn v
                    </Button>
                    <Button 
                      variant="primary" 
                      size="sm"
                      onClick={() => setShowAddZoneModal(true)}
                    >
                      <i className="fas fa-plus me-2"></i>
                      Thm kho
                    </Button>
                  </div>
                </div>

                {/* Zone Status Legend */}
                <div className="mb-3">
                  <div className="d-flex gap-3 flex-wrap">
                    <div className="d-flex align-items-center">
                      <div className="me-2" style={{ width: '16px', height: '16px', backgroundColor: '#dc3545', borderRadius: '2px' }}></div>
                      <span className="small">Cha cho thu ({stats.available})</span>
                    </div>
                    <div className="d-flex align-items-center">
                      <div className="me-2" style={{ width: '16px', height: '16px', backgroundColor: '#28a745', borderRadius: '2px' }}></div>
                      <span className="small"> cho thu ({stats.rented})</span>
                    </div>
                    <div className="d-flex align-items-center">
                      <div className="me-2" style={{ width: '16px', height: '16px', backgroundColor: '#ffc107', borderRadius: '2px' }}></div>
                      <span className="small"> cc ({stats.deposited})</span>
                    </div>
                    <div className="d-flex align-items-center">
                      <div className="me-2" style={{ width: '16px', height: '16px', backgroundColor: '#6c757d', borderRadius: '2px' }}></div>
                      <span className="small">Bo tr ({stats.maintenance})</span>
                    </div>
                  </div>
                </div>

                <div className="table-responsive">
                  <Table hover>
                    <thead className="bg-light">
                      <tr>
                        <th>M kho</th>
                        <th>Din tch</th>
                        <th>Trng thi</th>
                        <th>Gi thu</th>
                        <th>Khch thu</th>
                        <th>Thao tc</th>
                      </tr>
                    </thead>
                    <tbody>
                      {zones.map((zone) => (
                        <tr 
                          key={zone.id} 
                          style={{ cursor: 'pointer' }}
                          onClick={() => handleZoneClick(zone)}
                        >
                          <td>
                            <div>
                              <div className="fw-bold">{zone.zone_code}</div>
                              <small className="text-muted">{zone.zone_name}</small>
                            </div>
                          </td>
                          <td>{formatArea(zone.area)}</td>
                          <td>{getZoneStatusBadge(zone.status)}</td>
                          <td>
                            {zone.rental_price > 0 ? 
                              formatCurrency(zone.rental_price) + '/m/thng' : 
                              '-'
                            }
                          </td>
                          <td>
                            {zone.tenant_info ? (
                              <div>
                                <div className="fw-medium">{zone.tenant_info.company_name}</div>
                                <small className="text-muted">{zone.tenant_info.contact_person}</small>
                              </div>
                            ) : (
                              <span className="text-muted">-</span>
                            )}
                          </td>
                          <td>
                            <Button 
                              variant="outline-primary" 
                              size="sm"
                              onClick={(e) => {
                                e.stopPropagation();
                                handleZoneClick(zone);
                              }}
                            >
                              <i className="fas fa-eye"></i>
                            </Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              </Tab.Pane>

              {/* Map Tab */}
              <Tab.Pane eventKey="map">
                <ProjectMapView
                  project={project}
                  zones={zones}
                  onZoneClick={(zone) => {
                    setSelectedZone(zone);
                    setShowZoneModal(true);
                  }}
                  showLegend={true}
                />
              </Tab.Pane>

              {/* Tasks Tab */}
              <Tab.Pane eventKey="tasks">
                <TaskManager projectId={id} projectName={project?.name} />
              </Tab.Pane>

              {/* Documents Tab */}
              <Tab.Pane eventKey="documents">
                <h5 className="mb-3">H s php l</h5>
                <Row className="mb-4">
                  <Col md={6}>
                    <Card className="border-0 bg-light">
                      <Card.Body>
                        <div className="mb-2">
                          <small className="text-muted">Giy php kinh doanh</small>
                          <div className="fw-bold">{project.legal_documents?.business_license || 'Cha c'}</div>
                        </div>
                        <div className="mb-2">
                          <small className="text-muted">Giy php xy dng</small>
                          <div className="fw-bold">{project.legal_documents?.construction_permit || 'Cha c'}</div>
                        </div>
                        <div>
                          <small className="text-muted">Chng nhn PCCC</small>
                          <div className="fw-bold">{project.legal_documents?.fire_safety_cert || 'Cha c'}</div>
                        </div>
                      </Card.Body>
                    </Card>
                  </Col>
                </Row>

                <h5 className="mb-3">Ti liu d n</h5>
                
                {!isDocumentServiceAvailable ? (
                  <Alert variant="warning">
                    <Alert.Heading><i className="fas fa-exclamation-triangle me-2"></i>Dch v ti liu khng kh dng</Alert.Heading>
                    <p>
                      Khng th kt ni n dch v ti liu. Chc nng upload v xem file s b v hiu ha. 
                      Vui lng kim tra li kt ni hoc lin h qun tr vin.
                    </p>
                  </Alert>
                ) : (
                  <>
                    <div className="mb-3">
                      <Button 
                        variant="outline-primary" 
                        onClick={() => setShowDocumentModal(true)}
                      >
                        <i className="fas fa-upload me-2"></i>
                        Upload ti liu
                      </Button>
                    </div>

                    {uploadedDocuments.length > 0 ? (
                      <div className="table-responsive">
                        <Table hover className="border">
                          <thead className="bg-light">
                            <tr>
                              <th>Tn ti liu</th>
                              <th>Dung lng</th>
                              <th>Ngy upload</th>
                              <th style={{ width: '120px' }}>Thao tc</th>
                            </tr>
                          </thead>
                          <tbody>
                            {uploadedDocuments.map(doc => (
                              <tr key={doc.id}>
                                <td>
                                  <i className="fas fa-file me-2 text-primary"></i>
                                  {doc.name}
                                </td>
                                <td>{(doc.size / 1024).toFixed(2)} KB</td>
                                <td>{new Date(doc.uploadedAt).toLocaleDateString('vi-VN')}</td>
                                <td>
                                  <div className="d-flex gap-2">
                                    <Button
                                      variant="outline-primary"
                                      size="sm"
                                      title="Xem"
                                      onClick={() => handleViewDocument(doc)}
                                    >
                                      <i className="fas fa-eye"></i>
                                    </Button>
                                    <Button
                                      variant="outline-success"
                                      size="sm"
                                      title="Ti xung"
                                      onClick={() => handleDownloadDocument(doc)}
                                    >
                                      <i className="fas fa-download"></i>
                                    </Button>
                                    <Button
                                      variant="outline-danger"
                                      size="sm"
                                      title="Xa"
                                      onClick={() => handleDeleteDocument(doc.id)}
                                    >
                                      <i className="fas fa-trash"></i>
                                    </Button>
                                  </div>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </Table>
                      </div>
                    ) : (
                      <div className="text-center py-5 border rounded bg-light">
                        <i className="fas fa-file-alt fa-3x text-muted mb-3 d-block"></i>
                        <p className="text-muted mb-0">Cha c ti liu no c upload</p>
                      </div>
                    )}
                  </>
                )}
              </Tab.Pane>

              {/* Team Tab */}
              <Tab.Pane eventKey="team">
                <Row>
                  <Col lg={6} className="mb-4">
                    <Card className="border-0 shadow-sm">
                      <Card.Header className="bg-light border-0">
                        <h5 className="mb-0">
                          <i className="fas fa-user-tie me-2 text-primary"></i>
                          Gim c D n
                        </h5>
                      </Card.Header>
                      <Card.Body>
                        {project.project_director ? (
                          <div>
                            <div className="mb-3">
                              <div className="d-flex align-items-center mb-3">
                                <div className="bg-primary text-white rounded-circle p-3 me-3" style={{ width: '60px', height: '60px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                  <i className="fas fa-user fa-lg"></i>
                                </div>
                                <div>
                                  <h6 className="mb-0 fw-bold">{project.project_director.name}</h6>
                                  <small className="text-muted">{project.project_director.position}</small>
                                </div>
                              </div>
                            </div>
                            <div className="info-group">
                              <div className="info-item">
                                <strong>Email:</strong> {project.project_director.email}
                              </div>
                              <div className="info-item">
                                <strong>in thoi:</strong> {project.project_director.phone}
                              </div>
                            </div>
                          </div>
                        ) : (
                          <p className="text-muted text-center">Cha c gim c c ch nh</p>
                        )}
                      </Card.Body>
                    </Card>
                  </Col>

                  <Col lg={6} className="mb-4">
                    <Card className="border-0 shadow-sm">
                      <Card.Header className="bg-light border-0">
                        <h5 className="mb-0">
                          <i className="fas fa-clipboard-list me-2 text-info"></i>
                          Ngi Qun l D n
                        </h5>
                      </Card.Header>
                      <Card.Body>
                        {project.project_manager ? (
                          <div>
                            <div className="mb-3">
                              <div className="d-flex align-items-center mb-3">
                                <div className="bg-info text-white rounded-circle p-3 me-3" style={{ width: '60px', height: '60px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                  <i className="fas fa-user fa-lg"></i>
                                </div>
                                <div>
                                  <h6 className="mb-0 fw-bold">{project.project_manager.name}</h6>
                                  <small className="text-muted">{project.project_manager.position}</small>
                                </div>
                              </div>
                            </div>
                            <div className="info-group">
                              <div className="info-item">
                                <strong>Email:</strong> {project.project_manager.email}
                              </div>
                              <div className="info-item">
                                <strong>in thoi:</strong> {project.project_manager.phone}
                              </div>
                            </div>
                          </div>
                        ) : (
                          <p className="text-muted text-center">Cha c ngi qun l c ch nh</p>
                        )}
                      </Card.Body>
                    </Card>
                  </Col>
                </Row>
              </Tab.Pane>
            </Tab.Content>
          </Tab.Container>
        </Card.Body>
      </Card>

      {/* Zone Detail Modal */}
      <Modal show={showZoneModal} onHide={() => setShowZoneModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>
            Chi tit Kho {selectedZone?.zone_code}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedZone && (
            <Row>
              <Col md={6}>
                <h6>Thng tin kho</h6>
                <div className="info-group">
                  <div className="info-item">
                    <strong>M kho:</strong> {selectedZone.zone_code}
                  </div>
                  <div className="info-item">
                    <strong>Tn:</strong> {selectedZone.zone_name}
                  </div>
                  <div className="info-item">
                    <strong>Din tch:</strong> {formatArea(selectedZone.area)}
                  </div>
                  <div className="info-item">
                    <strong>Trng thi:</strong> {getZoneStatusBadge(selectedZone.status)}
                  </div>
                  <div className="info-item">
                    <strong>Gi thu:</strong> {selectedZone.rental_price > 0 ? formatCurrency(selectedZone.rental_price) + '/m/thng' : '-'}
                  </div>
                </div>
              </Col>
              <Col md={6}>
                <h6>Thng tin khch thu</h6>
                {selectedZone.tenant_info ? (
                  <div className="info-group">
                    <div className="info-item">
                      <strong>Cng ty:</strong> {selectedZone.tenant_info.company_name}
                    </div>
                    <div className="info-item">
                      <strong>Ngi lin h:</strong> {selectedZone.tenant_info.contact_person}
                    </div>
                    <div className="info-item">
                      <strong>S hp ng:</strong> {selectedZone.tenant_info.contract_number}
                    </div>
                    <div className="info-item">
                      <strong>Ngy ht hn:</strong> {new Date(selectedZone.tenant_info.end_date).toLocaleDateString('vi-VN')}
                    </div>
                  </div>
                ) : (
                  <p className="text-muted">Cha c khch thu</p>
                )}
              </Col>
            </Row>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowZoneModal(false)}>
            ng
          </Button>
          {hasPermission('zone_update') && (
            <Button variant="primary">
              Chnh sa kho
            </Button>
          )}
        </Modal.Footer>
      </Modal>

      {/* Upload Document Modal */}
      <Modal show={showDocumentModal} onHide={() => setShowDocumentModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Upload Ti liu D n</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="text-center py-5 border-2 border-dashed rounded" style={{ borderStyle: 'dashed' }}>
            <input
              type="file"
              multiple
              onChange={handleDocumentUpload}
              style={{ display: 'none' }}
              id="fileInput"
            />
            <label htmlFor="fileInput" style={{ cursor: 'pointer', width: '100%' }}>
              <i className="fas fa-cloud-upload-alt fa-3x text-primary mb-3 d-block"></i>
              <h6>Ko th hoc click  chn ti liu</h6>
              <small className="text-muted">H tr cc nh dng: PDF, DOC, DOCX, XLS, XLSX, IMG, JPG, PNG</small>
            </label>
          </div>

          {uploadedDocuments.length > 0 && (
            <div className="mt-3">
              <h6>Ti liu  chn ({uploadedDocuments.length}):</h6>
              <ul className="list-group">
                {uploadedDocuments.map(doc => (
                  <li key={doc.id} className="list-group-item d-flex justify-content-between align-items-center">
                    <span>
                      <i className="fas fa-file me-2"></i>
                      {doc.name}
                    </span>
                    <Badge bg="secondary">{(doc.size / 1024).toFixed(2)} KB</Badge>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDocumentModal(false)}>
            ng
          </Button>
          <Button 
            variant="primary"
            onClick={() => {
              setShowDocumentModal(false);
              if (uploadedDocuments.length > 0) {
                showNotification(` upload ${uploadedDocuments.length} ti liu thnh cng!`, 'success');
              }
            }}
          >
            Hon tt upload
          </Button>
        </Modal.Footer>
      </Modal>

      {/* Add/Edit Zone Modal */}
      <Modal show={showAddZoneModal} onHide={() => setShowAddZoneModal(false)} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>Thm Kho Mi</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="alert alert-info mb-3">
            <i className="fas fa-info-circle me-2"></i>
            Form thm kho s c hin th ti y
          </div>

          <Row>
            <Col md={6}>
              <Form.Group className="mb-3">
                <Form.Label>M Kho</Form.Label>
                <Form.Control type="text" placeholder="V d: A1" />
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group className="mb-3">
                <Form.Label>Tn Kho</Form.Label>
                <Form.Control type="text" placeholder="V d: Khu vc A1" />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col md={6}>
              <Form.Group className="mb-3">
                <Form.Label>Din tch (m)</Form.Label>
                <Form.Control type="number" placeholder="500" />
              </Form.Group>
            </Col>
            <Col md={6}>
              <Form.Group className="mb-3">
                <Form.Label>Gi thu (/m/thng)</Form.Label>
                <Form.Control type="number" placeholder="150000" />
              </Form.Group>
            </Col>
          </Row>

          <Form.Group className="mb-3">
            <Form.Label>Trng thi</Form.Label>
            <Form.Select>
              <option value="available">Cha cho thu</option>
              <option value="rented"> cho thu</option>
              <option value="deposited"> cc</option>
              <option value="maintenance">Bo tr</option>
            </Form.Select>
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowAddZoneModal(false)}>
            Hy
          </Button>
          <Button variant="primary" onClick={() => setShowAddZoneModal(false)}>
            Lu Kho
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default ProjectDetail;