/**
 * Warehouse Zone Component
 * V zone trn Google Map vi mu sc theo trng thi
 */

import React from 'react';
import { Polygon, InfoWindow } from '@react-google-maps/api';

// Mu sc theo trng thi
const STATUS_COLORS = {
  available: {
    fillColor: '#dc3545', //  - cha cho thu
    strokeColor: '#721c24',
    label: 'Cn trng'
  },
  rented: {
    fillColor: '#28a745', // Xanh -  cho thu
    strokeColor: '#155724',
    label: ' thu'
  },
  deposited: {
    fillColor: '#fd7e14', // Cam -  cc
    strokeColor: '#dc3545',
    label: ' cc'
  },
  fixed_service: {
    fillColor: '#ffffff', // Trng - c nh
    strokeColor: '#6c757d',
    label: 'Dch v c nh'
  },
  maintenance: {
    fillColor: '#ffc107', // Vng - bo tr
    strokeColor: '#856404',
    label: 'Bo tr'
  }
};

function WarehouseZone({ zone, onClick, isSelected }) {
  const [showInfo, setShowInfo] = React.useState(false);
  const [infoPosition, setInfoPosition] = React.useState(null);

  // Parse coordinates t JSON
  const paths = React.useMemo(() => {
    if (!zone.coordinates) return [];
    
    try {
      const coords = typeof zone.coordinates === 'string' 
        ? JSON.parse(zone.coordinates) 
        : zone.coordinates;
      
      // Chuyn i sang format Google Maps
      if (Array.isArray(coords)) {
        return coords.map(coord => ({
          lat: parseFloat(coord.lat || coord.latitude),
          lng: parseFloat(coord.lng || coord.longitude)
        }));
      }
      
      return [];
    } catch (error) {
      console.error('Error parsing coordinates:', error);
      return [];
    }
  }, [zone.coordinates]);

  // Tnh center ca polygon  t InfoWindow
  const getCenter = React.useCallback(() => {
    if (paths.length === 0) return null;
    
    const lat = paths.reduce((sum, p) => sum + p.lat, 0) / paths.length;
    const lng = paths.reduce((sum, p) => sum + p.lng, 0) / paths.length;
    
    return { lat, lng };
  }, [paths]);

  // Get color theo status
  const colorConfig = STATUS_COLORS[zone.status] || STATUS_COLORS.available;

  // Polygon options
  const polygonOptions = {
    fillColor: colorConfig.fillColor,
    fillOpacity: isSelected ? 0.6 : 0.4,
    strokeColor: isSelected ? '#000000' : colorConfig.strokeColor,
    strokeOpacity: isSelected ? 1 : 0.8,
    strokeWeight: isSelected ? 3 : 2,
    clickable: true,
    draggable: false,
    editable: false,
    geodesic: false,
    zIndex: isSelected ? 100 : 1
  };

  const handleClick = () => {
    const center = getCenter();
    if (center) {
      setInfoPosition(center);
      setShowInfo(true);
    }
    if (onClick) {
      onClick(zone);
    }
  };

  const handleMouseOver = () => {
    if (!isSelected) {
      setShowInfo(true);
      const center = getCenter();
      if (center) {
        setInfoPosition(center);
      }
    }
  };

  const handleMouseOut = () => {
    if (!isSelected) {
      setShowInfo(false);
    }
  };

  if (paths.length === 0) {
    return null;
  }

  return (
    <>
      <Polygon
        paths={paths}
        options={polygonOptions}
        onClick={handleClick}
        onMouseOver={handleMouseOver}
        onMouseOut={handleMouseOut}
      />
      
      {showInfo && infoPosition && (
        <InfoWindow
          position={infoPosition}
          onCloseClick={() => setShowInfo(false)}
        >
          <div style={{ maxWidth: '250px' }}>
            <h6 className="mb-2">
              <strong>{zone.zone_code}</strong>
              {zone.zone_name && ` - ${zone.zone_name}`}
            </h6>
            
            <div className="mb-2">
              <span 
                className="badge" 
                style={{ 
                  backgroundColor: colorConfig.fillColor,
                  color: zone.status === 'fixed_service' ? '#000' : '#fff'
                }}
              >
                {colorConfig.label}
              </span>
            </div>
            
            <div style={{ fontSize: '13px' }}>
              <div><strong>Din tch:</strong> {zone.area} m</div>
              
              {zone.zone_type && (
                <div><strong>Loi:</strong> {
                  zone.zone_type === 'rental' ? 'Cho thu' :
                  zone.zone_type === 'fixed_service' ? 'Dch v c nh' :
                  'Khu vc chung'
                }</div>
              )}
              
              {zone.rental_price && zone.status !== 'fixed_service' && (
                <div><strong>Gi thu:</strong> {zone.rental_price.toLocaleString('vi-VN')} VN/m</div>
              )}
              
              {zone.customer_info && zone.status === 'rented' && (
                <div className="mt-2 pt-2" style={{ borderTop: '1px solid #ddd' }}>
                  <strong>Khch thu:</strong>
                  <div>{zone.customer_info.name}</div>
                  {zone.customer_info.phone && (
                    <div> {zone.customer_info.phone}</div>
                  )}
                  {zone.customer_info.contract_end && (
                    <div>
                      <small>Ht hn: {new Date(zone.customer_info.contract_end).toLocaleDateString('vi-VN')}</small>
                    </div>
                  )}
                </div>
              )}
              
              {zone.facilities && Object.keys(zone.facilities).length > 0 && (
                <div className="mt-2">
                  <strong>Tin ch:</strong>
                  <div style={{ fontSize: '12px' }}>
                    {Object.entries(zone.facilities).map(([key, value]) => (
                      value && <span key={key} className="badge bg-secondary me-1">{key}</span>
                    ))}
                  </div>
                </div>
              )}
            </div>
            
            {onClick && (
              <button 
                className="btn btn-sm btn-primary mt-2 w-100"
                onClick={() => onClick(zone)}
              >
                Xem chi tit
              </button>
            )}
          </div>
        </InfoWindow>
      )}
    </>
  );
}

export default WarehouseZone;
