/**
 * Map Legend Component
 * Hin th ch thch mu sc zones
 */

import React from 'react';

const LEGEND_ITEMS = [
  { status: 'rented', color: '#28a745', label: ' cho thu', icon: '' },
  { status: 'available', color: '#dc3545', label: 'Cn trng', icon: '' },
  { status: 'deposited', color: '#fd7e14', label: ' nhn cc', icon: '' },
  { status: 'fixed_service', color: '#ffffff', label: 'Dch v c nh', icon: '', textColor: '#000' }
];

function MapLegend({ stats }) {
  return (
    <div className="map-legend">
      <div className="legend-header">
        <strong>Ch thch</strong>
      </div>
      <div className="legend-items">
        {LEGEND_ITEMS.map((item) => (
          <div key={item.status} className="legend-item">
            <span 
              className="legend-color"
              style={{ 
                color: item.color,
                textShadow: item.status === 'fixed_service' ? '0 0 2px #000' : 'none'
              }}
            >
              {item.icon}
            </span>
            <span className="legend-label">{item.label}</span>
            {stats && (
              <span className="legend-count">
                ({stats[item.status] || 0})
              </span>
            )}
          </div>
        ))}
      </div>
      
      <div className="legend-footer">
        <small className="text-muted">
          <i className="bi bi-info-circle"></i> Click vo zone  xem chi tit
        </small>
      </div>
    </div>
  );
}

export default MapLegend;
